import { driver } from "driver.js";
import "driver.js/dist/driver.css";
import { GET_LOCAL, SET_LOCAL } from "../utils";
import { getUserInfo_afterChanges, SkipOnBoarding } from "./loginActions";
import { TOGGLE_SHOPPABLE_MODAL } from "../reducer/keys";

class appTour {
    constructor(navigate, high = false, dispatch) {
        this.type = GET_LOCAL('tourType');
        this.dispatch = dispatch;
        this.tour = new driver({
            allowClose: high,
            stagePadding: 0,
            popoverOffset: 20,
            smoothScroll: true,
            onDestroyStarted: () => {},
            steps: getTourStep(this.type, navigate, this.nextStep, this.resetTour, this.stopTour, this.tourComplete)
        })
    }

    stopTour = () => this.tour.destroy()
    nextStep = () => this.tour.moveNext()
    resetTour = () => {
        localStorage.removeItem("tourStep");
        localStorage.setItem("isTour", true);
        this.stopTour();
    };
    tourComplete = () => {
        if(this.type == 1) this.dispatch({ type: TOGGLE_SHOPPABLE_MODAL, payload: true });
        SkipOnBoarding({ stepstype: "appTour" }).then(res => {
            this.dispatch(getUserInfo_afterChanges());
        }).catch(ex => {})
    }
}

export default appTour;


